/* mode ipad */
@media screen and (max-width: 768px){
    #blog_site{
        width: 100%;
    }
    .section{
        width: 92%;
        height: 467.09px;
        margin: auto;
        background-color: white;
    }
    .text-description{
        width: 90%;
        height: 80%;
        margin: 0 auto 0;
        padding-top: 50px;
    }
    .text-description .nameLastName{
        width: 500px;
        height: 118px;
        margin: 0 auto 0;
        padding-top: 5%;
        font-family: Inter;
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 123.6%;
        color: #2B2B2B;
    }
    .domaine{
        color: #FFB400;
    }
    .description-more{
        width: 424px;
        text-align: center;
        height: 88px;
        margin: 0 auto;
        font-family: Inter;
        font-style: normal;
        font-weight: 900;
        font-size: 16px;
        line-height: 24px;
        /* or 150% */
        text-transform: capitalize;
        font-feature-settings: 'calt' off, 'kern' off;
        /* Paragraph Font */
        color:#767676;
    }
    .btn-block{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 50px;
        border: none;
    }
    .btn{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 16px 32px;
        background: #FFB400;
        border-radius: 5px;
        border: none;
        cursor: pointer;

        /* Inside Auto Layout */

        flex: none;
        order: 0;
        flex-grow: 0;
        margin: 0px 0px;
    }
    .mes-services .text-service .titre-service{
        font-family: Inter;
        font-style: normal;
        font-weight: bold;
        font-size: 32px;
        line-height: 123.6%;
        /* identical to box height, or 40px */

        text-transform: capitalize;

        /* Black */

        color: #2B2B2B;
    }
    .mes-services{
        padding-top: 50px;
    }
    .mes-services .text-service .paragraphe-service{
        margin: 0 auto;
        /* P-15px paragraph */
        width: 438px;
        height: 68px;
        font-family: Inter;
        font-style: normal;
        font-weight: 900;
        font-size: 15px;
        line-height: 24px;
        /* or 160% */

        text-align: center;
        text-transform: capitalize;
        font-feature-settings: 'calt' off, 'kern' off;
        padding-bottom: 50px;

        /* Paragraph Font */
        color: #767676;
    }
    .mes-services .tech-services{
        width: 90%;
        height: 600px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 50px;
        margin-bottom: 100px;
        display: flex;
        flex-wrap:wrap;
        justify-content: space-between;
    }
    .mes-services .tech-services .tech-metier{
        width: 220px;
        height: 285px;
        background-color: white;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .mes-services .tech-services .tech-metier .img{
        margin-bottom: 52px;
    }
    .mes-services .tech-services .tech-metier .img img{
        width: 48px;
        height: 48px;
        color: #FFB400;
    }
    .mes-services .tech-services .tech-metier h3{
        width: 158px;
        height: 22px;
        font-family: Inter;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 123.6%;
        margin: 0 auto;
        /* or 22px */

        text-transform: capitalize;
        color: #2B2B2B;

    }
    .mes-services .tech-services .tech-metier p{
        width: 134px;
        height: 24px;
        margin: 0 auto;
        font-family: Inter;
        font-style: normal;
        font-weight: 900;
        font-size: 13px;
        line-height: 24px;
        /* identical to box height, or 160% */
        text-align: center;
        text-transform: capitalize;
        font-feature-settings: 'calt' off, 'kern' off;
        /* Paragraph Font */
        color: #767676;
    }
    .prices{
        width: 90%;
        height: 800px;
        margin: 0 auto 150px;
    }
    .prices h3{
        font-family: Inter;
        font-style: normal;
        font-weight: bold;
        font-size: 32px;
        line-height: 123.6%;
        /* identical to box height, or 40px */
        text-transform: capitalize;
        color: #2B2B2B;
    }
    .prices .mes-offres{
        display: flex;
        justify-content: space-between;
    }
    .prices .mes-offres .prices-description{
