@media screen and (max-width:768px){
    .education{
        width: 98%;
        height: 632px;
        margin: 0 auto 0;
        padding: 100px 0 0;
    }
    .education .nameEducation{
        width: 158px;
        height: 40px;
        margin: auto;
        font-family: Inter;
        font-style: normal;
        font-weight: bold;
        font-size: 32px;
        line-height: 123.6%;
        /* identical to box height, or 40px */

        text-transform: capitalize;

        /* Black */

        color: #2B2B2B;
    }
    .education .citation{
        width: 338px;
        height: 68px;
        margin: auto;
        /* P-15px paragraph */

        font-family: Inter;
        font-style: normal;
        font-weight: 900;
        font-size: 15px;
        line-height: 24px;
        /* or 160% */

        text-align: center;
        text-transform: capitalize;
        font-feature-settings: 'calt' off, 'kern' off;

        /* Paragraph Font */

        color: #767676;

    }
    .school-year{
        background-color: white;
        width: 98%;
        height: 200px;
        margin: auto;
        display: flex;
        justify-content: space-around;
    }
    .year-student{
        width: 50%;
    }
    .year-student .student{
        display: flex;
        width: 60%;
        justify-content: space-between;
        align-items: baseline;
        margin-left: 20px;
    }
    .year-student .student .student-text{
        font-family: Inter;
        font-style: normal;
        font-weight: 900;
        font-size: 15px;
        line-height: 24px;
        /* or 160% */

        text-transform: capitalize;
        font-feature-settings: 'calt' off, 'kern' off;

        /* Paragraph Font */

        color: #767676;

    }
    .year-student .student .year{
        width: 111px;
        height: 18px;
        background-color: #FFB400;
        font-family: Inter;
        font-style: normal;
        font-weight: 900;
        font-size: 10px;
        line-height: 100.9%;
        /* identical to box height, or 10px */


        /* White */

        color: #FFFFFF;
        line-height: 18px;

    }
    .school{
        margin-right: 20px;
    }
    .school div h2{
        font-family: Inter;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 123.6%;
        /* or 22px */
        text-transform: capitalize;
        /* Black */
        color: #2B2B2B;
        margin-bottom: 0;
        margin-top: 10px;
    }
    .school div span{
        font-family: Inter;
        font-style: normal;
        font-weight: 900;
        font-size: 10px;
        line-height: 24px;
        /* or 160% */

        text-transform: capitalize;
        font-feature-settings: 'calt' off, 'kern' off;

        /* Paragraph Font */

        color: #767676;

    }
}
