Link:active{
    background-color: red;
}
@media screen and (max-width: 768px) {
    .bar-responsive{
        width: 100%;
        position: relative;
    }
    .bar-responsive-list{
        position: fixed;
        display: flex;
        justify-content: space-between;
        flex-direction: row;
        align-items:baseline;
        left:0;
        right: 0;
        background-color: white;
    }
    .menu-hamburger{
        margin-left: 15px;
        color: black;
        /* margin-bottom: 20px; */
    }
    .icone-right{
        margin-right: 15px;
    }
    /* menu qui se trouve sur le footer */
    .navbar-mobile{
        position: absolute;
        bottom: 0;
        width: 100%;
        overflow: hidden;
        position: fixed;
        background-color: white;
    }
    .navbar-mobile .navbar-mobile-list{
        list-style: none;
        display: flex;
        flex-direction: row;
        justify-content:space-evenly;
    }
    .navbar-mobile .navbar-mobile-list .list-menu{
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background: #E5E5E5;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
        position: relative;
    }
    .navbar-mobile .navbar-mobile-list .list-menu img{
        width: 20px;
        height: 20px;
        display: block;
        margin:auto;
        position: absolute;
        right: 10px;
        top: 10px;
    }
    .text-menu{
        display: none;
    }
}
@media screen and (max-width: 360px){
    .navbar-mobile{
        bottom: 0;
        width: 100%;
        overflow: hidden;
        position: fixed;
    }
    .navbar-mobile .navbar-mobile-list{
        list-style: none;
        display: flex;
        flex-direction: row;
        justify-content:space-between;
        margin-left: -30px;
        margin-right: 10px;
    }
}
@media screen and (max-width: 240px){
    .navbar-mobile{
        bottom: 0;
        width: 100%;
        overflow: hidden;
        position: fixed;
    }
    .navbar-mobile .navbar-mobile-list{
        list-style: none;
        display: flex;
        flex-direction: row;
        justify-content:space-between;
    }
    .navbar-mobile .navbar-mobile-list .list-menu{
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background: #E5E5E5;
        position: relative;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }
    .navbar-mobile .navbar-mobile-list .list-menu img{
        width: 10px;
        height: 10px;
        display: block;
        margin:auto;
        position: absolute;
        right: 4px;
        top: 4px;
    }
}
@media screen and (min-width:769px) and (max-width: 1024px) {
    #navbar-mobile{
        position: fixed;
        width: 50px;
        height: 100vh;
        right: 0;
        top: -15px;
        background-color: white;
    }
    .navbar-mobile{
        width: 100%;
        background-color: aquamarine;
    }
    .navbar-mobile .navbar-mobile-list{
        list-style: none;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .navbar-mobile .navbar-mobile-list .text-menu{
        display: none;
    }
}