@media screen and (max-width: 768px) {
    .contact{
        width: 95%;
        height: 768px;
        margin: auto;
        padding-top: 100px;
        display: flex;
        justify-content: space-between;
        
    }
    .info-perso{
        width: 60%;
    }
    .info-details{
        width: 92%;
        height: 95%;
        margin: auto;
        background-color: white;
    }
    .info-perso .info-details form div{
        display: block;
        text-align: left;
    }
    .info-perso .info-details form div input{
        width: 92%;
        height: 30px;
        background: #F0F0F6;
        border: none;
    }
    .info-perso .info-details form div textarea{
        background: #F0F0F6;
        width: 92%;
        border: none;
        height: 300px;
    }
    /* .form-class{
        margin-bottom: 25px;
    } */
    .coord{
        width: 38%;
        
    }
}